


import React, { useEffect, useState } from 'react'
import "../../Styles/pages/cartPage.css"
import { useGlobal } from '../context/GlobalContext'
const CartPage = () => {
  const [cartItems, setCartItems] = useState([])


  const {state,dispatch} = useGlobal();

  console.log("CartPage state", state)
// set cartItems to the cart from global state
  useEffect(() => {
    if (state.cart && state.cart.length > 0) {
      setCartItems(state.cart);
    } else {
      setCartItems([]);
    }
  }, [state.cart]);

  
  const [couponCode, setCouponCode] = useState('')
  const [shippingOption, setShippingOption] = useState('free')

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity < 1) return
    setCartItems(items =>
      items.map(item =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    )
  }

  const removeItem = (id) => {
    // setCartItems(items => items.filter(item => item.id !== id))
        dispatch({ type: "REMOVE_FROM_CART", payload: id });


  }

  const applyCoupon = () => {
    if (couponCode.trim()) {
      alert(`Coupon "${couponCode}" applied!`)
      setCouponCode('')
    }
  }

  const subtotal = cartItems.reduce((sum, item) => sum + (item.price * item.quantity), 0)
  const shippingCost = shippingOption === 'flat' ? 10 : 0
  const total = subtotal + shippingCost

  return (
    <div className="cart-page-wrapper">
     <div className='container'>
       <div className="cart-page-header">
        <h1 className="cart-page-title">Cart</h1>
        <div className="cart-page-breadcrumb">
          <span className="cart-page-breadcrumb-home">Home</span>
          <span className="cart-page-breadcrumb-separator">›</span>
          <span className="cart-page-breadcrumb-current">Cart</span>
        </div>
    </div>

      <div className="cart-page-content">
        <div className="cart-page-main">
          <div className="cart-page-section">
            <h2 className="cart-page-section-title">Product List</h2>
            
            <div className="cart-page-items">
             {
              cartItems.length > 0 && (
                <>
                 {cartItems.map(item => (
                <div key={item.id} className="cart-page-item">
                  <div className="cart-page-item-image">
                    <img src={item.images[0]} alt={item.name} />
                  </div>
                  <div className="cart-page-item-details">
                    <div className="cart-page-item-brand">Nightly</div>
                    <div className="cart-page-item-name">{item.name}</div>
                  </div>
                  <div className="cart-page-item-price">
                    ${item.price.toFixed(2)}
                  </div>
                  <div className="cart-page-item-quantity">
                    <button 
                      className="cart-page-quantity-btn"
                      onClick={() => updateQuantity(item.id, item.quantity - 1)}
                    >
                      -
                    </button>
                    <span className="cart-page-quantity-value">{item.quantity}</span>
                    <button 
                      className="cart-page-quantity-btn"
                      onClick={() => updateQuantity(item.id, item.quantity + 1)}
                    >
                      +
                    </button>
                  </div>
                  <button 
                    className="cart-page-remove-btn"
                    onClick={() => removeItem(item.id)}
                  >
                    Remove
                  </button>
                </div>
              ))}
                </>
              )
             
             }
             {
              cartItems.length >=0 && (
                <>
                  <span className='items-not-avaliable'>
                No Products
                    
                    </span>                  
                </>
              )
             }
            </div>

            <div className="cart-page-coupon">
              <div className="cart-page-coupon-section">
                <label className="cart-page-coupon-label">Coupon Code:</label>
                <div className="cart-page-coupon-input-group">
                  <input
                    type="text"
                    className="cart-page-coupon-input"
                    placeholder="Apply coupon here"
                    value={couponCode}
                    onChange={(e) => setCouponCode(e.target.value)}
                  />
                  <button 
                    className="cart-page-coupon-btn"
                    onClick={applyCoupon}
                  >
                    Apply
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="cart-page-sidebar">
          <div className="cart-page-summary">
            <div className="cart-page-summary-row">
              <span className="cart-page-summary-label">Subtotal</span>
              <span className="cart-page-summary-value">${subtotal.toFixed(2)}</span>
            </div>

            <div className="cart-page-shipping">
              <div className="cart-page-summary-label">Shipping</div>
              <div className="cart-page-shipping-options">
                <label className="cart-page-shipping-option">
                  <input
                    type="radio"
                    name="shipping"
                    value="flat"
                    checked={shippingOption === 'flat'}
                    onChange={(e) => setShippingOption(e.target.value)}
                  />
                  <span className="cart-page-shipping-text">Flat rate</span>
                </label>
                <label className="cart-page-shipping-option">
                  <input
                    type="radio"
                    name="shipping"
                    value="free"
                    checked={shippingOption === 'free'}
                    onChange={(e) => setShippingOption(e.target.value)}
                  />
                  <span className="cart-page-shipping-text">Free shipping</span>
                </label>
              </div>
            </div>

            <div className="cart-page-shipping-address">
              <span className="cart-page-shipping-to">Shipping to <strong>NY</strong></span>
              <button className="cart-page-change-address">Change address</button>
            </div>

            <div className="cart-page-total">
              <div className="cart-page-summary-row">
                <span className="cart-page-summary-label">Total</span>
                <span className="cart-page-summary-value">${total.toFixed(2)}</span>
              </div>
            </div>

            <button className="cart-page-checkout-btn">
              Proceed To Checkout
            </button>

            <button className="cart-page-continue-shopping">
              ← Continue Shopping
            </button>
          </div>
        </div>
      </div>
     </div>
    </div>
  )
}

export default CartPage